.featured {
    flex: 2;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    padding: 10px;

    .top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: gray;

        .title {
            font-size: 16px;
            font-weight: 500;
        }
    }

    .bottom {
        padding: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 15px;

        .featuredChart {
            width: 100px;
            height: 100px;
        }

        .title {
            font-weight: 500;
            color: gray;
        }

        .amount {
            font-size: 30px;
        }

        .desc {
            font-size: 12px;
            font-weight: 300;
            color: gray;
            text-align: center;
        }

        .summary {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;

            .item {

                .itemTitle {
                    text-align: center;
                    font-size: 14px;
                    color: gray;
                }
                .itemResult{

                    display:flex;
                    align-items:center;
                    margin-top: 10px;
                    font-size: 14px;

                    &.positive{
                        color:greenyellow;
                    }
                    &.negative{
                        color:red;
                    }
                }

            }
        }

    }
}